import React from "react";
import styled from "styled-components";

import "./Skills.css";

import LogoHTML from "../Assets/Images/html-logo.jpg";
import LogoNodeJS from "../Assets/Images/node.js_logo.png";
import LogoMongo from "../Assets/Images/mongodb-logo.jpg";
import logoCobol from "../Assets/Images/Cobol.jpg";

import LogoCSS from "../Assets/Images/CSS2.png";
import logoJavaScript from "../Assets/Images/javascript-logo.png";
import logoMySQL from "../Assets/Images/MySql.png";
import logoDB2 from "../Assets/Images/DB2.jpg";

import logoBootstrap from "../Assets/Images/bootstrap_logo.png";
import logoExpress from "../Assets/Images/ExpressJS.png";
import logogFirebase from "../Assets/Images/Firebase.jpg";
import logogIMS from "../Assets/Images/IMS.png";

import logogReact from "../Assets/Images/react-logo.svg";
import logogJQuery from "../Assets/Images/JQuery.png";

const MediaImg = styled.img`
    width: 100px;
    height: 75px;
    display: block;
    margin-left: auto;
    margin-right: auto;

    @media only screen and (max-width: 414px) {
        padding-right: 10px;
        width: 50px;
        height: 50px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
`;

const styles = {
    row: {
        marginTop: "18px",
    },
};

function Home() {
    return (
        <React.Fragment>
            <div className="container-fluid">
                <div style={styles.row} className="row">
                    <div className="col-4"></div>
                    <div className="col-4">
                        <h1>My Skill Set</h1>
                    </div>
                    <div className="col-4"></div>
                </div>
                <div style={styles.row} className="row">
                    <div className="col-3">
                        <h2>FRONT END</h2>
                    </div>
                    <div className="col-3">
                        <h2>SERVER SIDE</h2>
                    </div>
                    <div className="col-3">
                        <h2>BACK END</h2>
                    </div>
                    <div className="col-3">
                        <h2>MAINFRAME</h2>
                    </div>
                </div>
                <div style={styles.row} className="row">
                    <div className="col-3">
                        <MediaImg src={LogoHTML} alt="HTML logo" />
                        <h3>HTML</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={LogoNodeJS} alt="NodeJS logo" />
                        <h3>Node.js</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={LogoMongo} alt="Mongo logog" />
                        <h3>Mongo DB</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logoCobol} alt="Cobol logo" />
                        <h3>COBOL</h3>
                    </div>
                </div>

                <div style={styles.row} className="row">
                    <div className="col-3">
                        <MediaImg src={LogoCSS} alt="CSS logo" />
                        <h3>CSS</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logoJavaScript} alt="JavaScript logo" />
                        <h3>JavaScript</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logoMySQL} alt="MySQL logo" />
                        <h3>MySQL</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logoDB2} alt="DB2 logo" />
                        <h3>DB2</h3>
                    </div>
                </div>
                <div style={styles.row} className="row">
                    <div className="col-3">
                        <MediaImg src={logoBootstrap} alt="Bootstrap logo" />
                        <h3>BootStrap</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logoExpress} alt="Express js logo" />
                        <h3>Express JS</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logogFirebase} alt="Firebase logo" />
                        <h3>FireBase</h3>
                    </div>

                    <div className="col-3">
                        <MediaImg src={logogIMS} alt="IMS logo" />
                        <h3>IMS</h3>
                    </div>
                    <div className="col-3"></div>
                </div>
                <div style={styles.row} className="row">
                    <div className="col-3">
                        <MediaImg src={logogReact} alt="React logo" />
                        <h3>React JS</h3>
                    </div>

                    <div className="col-9"></div>
                </div>
                <div style={styles.row} className="row">
                    <div className="col-3">
                        <MediaImg src={logogJQuery} alt="JQuery logo" />
                        <h3>JQuery</h3>
                    </div>

                    <div className="col-9"></div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default Home;
