import React from "react";
import styled from "styled-components";
//import image1 from "./Images/MyPic.jpg";

//import image1 from "Images/day_planner.png";

const MediaDiv = styled.div`
    width: 100%;
    background-color: black;
    padding-left: 300px;
    padding-right: 300px;

    @media only screen and (max-width: 1024px) {
        margin: auto;
        width: 100%;
        padding-left: 100px;
        padding-right: 100px;
    }

    @media only screen and (max-width: 414px) {
        margin: auto;
        width: 360px;
        padding-left: 0px;
        padding-right: 0px;
    }

    @media only screen and (max-width: 320px) {
        width: 320px;
        padding-left: 0px;
        padding-right: 0px;
    }
`;

const MediaImg = styled.img`
    height: 300px;
    width: 400px;
    display: block;
    margin-left: auto;
    margin-right: auto;

    @media only screen and (max-width: 414px) {
        height: 200px;
        width: 340px;
    }

    @media only screen and (max-width: 320px) {
        height: 200px;
        width: 315px;
    }
`;
const MediaAnchor = styled.a`
    color: silver;
    font-size: 25px;
    padding: 55px;

    @media only screen and (max-width: 414px) {
        font-size: 15px;
        padding: 0px;
    }
`;

const styles = {
    AboutMe: {
        fontSize: "35px",
        backgroundColor: "black",
        padding: "3%",
    },

    fillerRed: {
        backgroundColor: "black",
        color: "red",
    },

    fillerGreen: {
        backgroundColor: "darkred",
        alignContent: "center",
        marginTop: "0px",
        padding: "10px",
    },

    container: {
        marginBottom: "25px",
    },
};

//<div style={styles.container} className="container-fluid">
// Whenever we try to render an array containing JSX, React knows to render each JSX element separately
function Portfolio(props) {
    const notGood = props.projects;

    return (
        <MediaDiv>
            <div style={styles.fillerRed} className="row">
                <div className="col-12">
                    <h1 style={styles.AboutMe}>Portfolio For Jim Denis</h1>
                </div>
            </div>
            {notGood.map((item) => (
                <div
                    style={styles.container}
                    className="container-fluid"
                    key={item.id}
                >
                    <div className="row">
                        <div style={styles.fillerGreen} className="col-12">
                            <h2>{item.title}</h2>
                        </div>
                    </div>

                    <div className="row">
                        <div style={styles.fillerGreen} className="col-12">
                            <MediaImg
                                src={item.image}
                                alt="Project Pic"
                            ></MediaImg>
                        </div>
                    </div>

                    <div className="row">
                        <div style={styles.fillerGreen} className="col-12">
                            <h2>GitHub URL</h2>

                            <MediaAnchor href={item.gitHub}>
                                {item.gitHub}
                            </MediaAnchor>

                            <h2>Website URL</h2>

                            <MediaAnchor href={item.gitUrl}>
                                {item.gitUrl}
                            </MediaAnchor>
                        </div>
                    </div>
                </div>
            ))}
        </MediaDiv>
    );
}

export default Portfolio;
