import React from "react";
import styled from "styled-components";
import resume from "../Assets/Docs/Resume.docx";
//import image1 from "./Images/MyPic.jpg";

//import image1 from "Images/day_planner.png";

const MediaDiv = styled.div`
    width: 100%;
    background-color: black;
    padding-left: 300px;
    padding-right: 300px;

    @media only screen and (max-width: 1024px) {
        margin: auto;
        width: 100%;
        padding-left: 100px;
        padding-right: 100px;
    }

    @media only screen and (max-width: 414px) {
        margin: auto;
        width: 360px;
        padding-left: 0px;
        padding-right: 0px;
    }

    @media only screen and (max-width: 320px) {
        width: 320px;
        padding-left: 0px;
        padding-right: 0px;
    }
`;

const MediaAnchor = styled.a`
    color: silver;
    font-size: 30px;
    padding: 70px;

    @media only screen and (max-width: 414px) {
        font-size: 15px;
        padding: 10px;
    }
`;

const styles = {
    AboutMe: {
        fontSize: "35px",
        backgroundColor: "black",
        padding: "3%",
    },

    fillerRed: {
        backgroundColor: "black",
        color: "red",
    },

    fillerGreen: {
        backgroundColor: "darkred",
        alignContent: "center",
        marginTop: "0px",
        padding: "10px",
    },

    container: {
        marginBottom: "25px",
    },
};

//<div style={styles.container} className="container-fluid">
// Whenever we try to render an array containing JSX, React knows to render each JSX element separately
function Links() {
    return (
        <MediaDiv>
            <div style={styles.fillerRed} className="row">
                <div className="col-12">
                    <h1 style={styles.AboutMe}>Links For Jim Denis</h1>
                </div>
            </div>

            <div className="row">
                <div style={styles.fillerGreen} className="col-12">
                    <h2>LinkedIn</h2>

                    <MediaAnchor href="www.linkedin.com/in/jimdenis">
                        www.linkedin.com/in/jimdenis
                    </MediaAnchor>

                    <h2>GetHub</h2>

                    <MediaAnchor href="https://github.com/JimDenis">
                        https://github.com/JimDenis
                    </MediaAnchor>

                    <h2>Email</h2>

                    <MediaAnchor href="mailto:jimdenis007@yahoo.com">
                        JimDenis007@yahoo.com
                    </MediaAnchor>

                    <h2>Cell Phone</h2>

                    <MediaAnchor href="tel:6026790390">
                        (602)679-0390
                    </MediaAnchor>

                    <h2>Resume</h2>

                    <MediaAnchor href={resume}>Resume</MediaAnchor>
                </div>
            </div>
        </MediaDiv>
    );
}

export default Links;
